body {
    padding : 0;
    margin: 0;
}

.container {
    width: 1120px;
    margin: auto;
}
.table {
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    
    .textCenter {
        text-align: center;
    }
}
.navLink{
    a {
        padding : 0px 5px;
    }
    font-size: 20px;
    cursor: pointer;
    text-decoration: none;
    padding-bottom: 2px;
    
    color: #222;

    &:hover {
        color: #22b8cf;
    }

    &:after {
        content: '|';
        display: inline-block;
        padding: 0 7px;
        color: #ccc;
    }

    &:last-child {
        &:after {
            color: #fff;
        }
    }

    &.active {
        text-decoration: underline;
        color: #22b8cf;
        &:after {
            border-bottom: 4px solid #fff !important;
        }
    }
}